import context from '@/main.js'
import axios from 'axios';                                                    // axios
import { Message} from 'element-ui';                                          // element
import store from '@/store/index'                                             // vuex
import { getToken, removeToken } from '@/utils/auth'                          // token

// axios
const service = axios.create({
  baseURL: process.env.BASE_API,                                           // apibase_url
  // baseURL: '/api',                                                            // apibase_url
  timeout: 5000                                                               // 
})

// request 
service.interceptors.request.use(
  config => {
    // context.bus.$emit('loading', true)
    return config
  },
  error => {
    return Promise.error(error)
  }
)

// response 
service.interceptors.response.use(
  response => {
    // setTimeout(()=>{
    //   context.bus.$emit('loading', false)
    // },500)
    if(response.data.result == 0){                                            //   
      return response
    }else if(response.data.result == 2){
      removeToken()
      // localStorage.clear()
      Message({
        message: '',
        type: 'warning',
        duration: 3000
      })
      window.location.reload()
    }else{
      Message({
        message: response.data.info,
        type: 'warning',
        duration: 3000
      })
      setTimeout(() => {return response },3000)
    }
  },
  error => {
    // context.bus.$emit('loading', false)
    Message({
      message:'',
      type:'error',
      duration:10*1000
    })
    return Promise.reject(error)
  }
)
export default service;



